*, *::before, *::after {
    box-sizing: border-box;
    /* outline: auto; */
    line-height: 1.5;
    font-size: 16px;
}
.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
}

@media (max-width: 768px) {
    .container {
        max-width: 720px;
    }
}

@media (max-width: 375px) {
    .container {
        max-width: 375px;
    }
}

.title {
    text-align: center;
    font-size: 48px;
    font-weight: bold;
}

@media (max-width: 768px) {
    .title {
        font-size: 32px;
    }
}

@media (max-width: 375px) {
    .title {
        font-size: 24px;
    }
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}

.bg-primary {
    background-color: #AA0601;
}

.bg-light {
    background-color: white;
}

.bg-secondary {
    background-color: #AA06010D;
}

/* header----------------------- */

.navbar {
    padding: 30px 88px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav_logo a {
    display: block;
    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
}

.nav_button {
    display: flex;
    justify-content: space-between;
}

.nav_button a {
    margin-left: 64px;
    text-decoration: none;
    color: white;
    font-size: 20px;
}

.banner {
    background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner.png);
    height: 840px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.banner .container {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
}

.slogan {
    text-align: right;
    color: #650300;
    line-height: 1.5;
}

.slogan_headtitle {
    font-size: 64px;
    font-weight: bold;
}

.slogan_subtitle {
    font-size: 36px;
}

@media (max-width:768px) {
    .navbar {
        padding: 30px;
    }
    .nav_button a {
        margin-left: 40px;
    }
    .slogan_headtitle {
        font-size: 48px;
        font-weight: bold;
    }
    .slogan_subtitle {
        font-size: 20px;
    }
}

@media (max-width:375px) {
    .navbar {
        flex-direction: column;
        padding: 0;
    }
    .nav_logo img {
        width: 60%;
        margin: 0 auto;
    }
    .nav_logo a {
        margin-bottom: 10px;
    }
    .nav_button {
        width: 100%;
        flex-wrap: wrap;
        justify-content: center;
        border: 1px solid #fff;
    }
    .nav_button li {
        width: 50%;
        text-align: center;
        padding-top: 8px;
        padding-bottom: 8px;
    }
    .series, .store {
        border-bottom: 1px solid #fff;
    }
    .series, .blog {
        border-right: 1px solid #fff;
    }
    .nav_button a {
        margin: 0;
    }
    .banner {
        height: 394px;
    }
    .slogan_headtitle {
        font-size: 32px;
    }
}

/* feature----------------------- */

.feature {
    padding-top: 60px;
    padding-bottom: 80px;
}

.feature .title {
    color: #650300
}

.special {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 48px;
}

.special li {
    width: 255px;
    padding: 0 15px;
}

.special :first-child {
    padding-left: 0;
}

.special :last-child {
    padding-right: 0;
}

.special img {
    margin: 0 auto;
    margin-bottom: 24px;
}

.special h4 {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: #650300;
    margin-bottom: 8px;
}

.special p {
    color: black;
}

@media (max-width: 768px) {
    .feature {
        padding-top: 40px;
        padding-bottom: 12px;
    }
    .special {
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .special li {
        padding: 0;
        margin-bottom: 44px;
    }
}

/* footer---------------------------- */

.footer {
    padding-top: 32px;
    padding-bottom: 40px;
}

.footer-menu {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
}

.footer-menu .cotact {
    display: flex;
    align-items: center;
}

.footer-menu .cotact li {
    margin-right: 48px;
}

.footer-menu .cotact a {
    display: block;
    color: white;
    text-decoration: none;
}

.footer-icon {
    display: flex;
    align-items: center;
    justify-content: end;
}

.footer-icon li {
    margin-left: 32px;
}

.tel, .mail {
    display: block;
    text-decoration: none;
    color: white;
    font-size: 36px;
}

.footercontact-info img {
    margin-right: 20px;
    display: inline;
}

.copyright {
    margin-top: 24px;
    border-top: 1px solid #fff;
    padding-top: 32px;
    display: flex;
    justify-content: space-between;
}

.copyright p {
    color: white;
}

.copyright-link {
    display: flex;
}

.copyright-link li {
    margin-left: 48px;
}

.copyright-link a {
    text-decoration: none;
    color: #fff;
}

@media (max-width: 768px) {
    .footer-menu .cotact li {
        margin-right: 40px;
    }
    .footer-icon li {
        width: 24px;
        height: 24px;
        margin-left: 24px;
    }
    .footer-icon img {
        width: 100%;
    }
    .copyright-link li {
        margin-left: 24px;
    }
    .tel, .mail {
        font-size: 20px;
    }
    .footercontact-info img {
        width: 20px;
        margin-right: 10px;
    }
    .copyright-link a {
        font-size: 14px;
    }
}

@media (max-width: 375px) {
    .footer {
        position: relative;
        padding-bottom: 16px;
        padding-top: 24px;

    }
    .footer-menu {
        position: absolute;
        z-index: 5;
        top: 24px;
        left: 216px;
    }
    .footer-menu .cotact {
        display: none;
    }
    .footer-menu {
        justify-content: flex-end
    }
    .tel, .mail {
        font-size: 16px;
    }
    .copyright {
        font-size: 14px;
        display: block;
        margin-top: 24px;
        padding-top: 16px;
    }
    .copyright-link {
        display: block;
    }
    .copyright p {
        margin-bottom: 8px;
    }
    .copyright-link li {
        margin-left: 0px;
        margin-bottom: 8px;
    }
}